'use client';

import { useEffect } from 'react';

export default function ServiceWorkerRegistration() {
  useEffect(() => {
    if ('serviceWorker' in navigator) {
      const registerSW = async () => {
        try {
          const registration = await navigator.serviceWorker.register('/sw.js', {
            scope: '/'
          });
          
          console.log('Service Worker registered successfully:', registration);
          
          // Service Worker g√ºncellemelerini kontrol et
          registration.addEventListener('updatefound', () => {
            const newWorker = registration.installing;
            
            if (newWorker) {
              newWorker.addEventListener('statechange', () => {
                if (newWorker.state === 'installed' && navigator.serviceWorker.controller) {
                  // Yeni Service Worker kuruldu, kullanƒ±cƒ±ya bildir
                  if (confirm('Yeni g√ºncelleme mevcut! Sayfayƒ± yenilemek ister misiniz?')) {
                    window.location.reload();
                  }
                }
              });
            }
          });
          
          // Offline durumu kontrol et
          const checkOnlineStatus = () => {
            if (!navigator.onLine) {
              console.log('App is offline');
              // Offline durumunda kullanƒ±cƒ±ya bildirim g√∂ster
              document.body.classList.add('offline');
            } else {
              console.log('App is online');
              document.body.classList.remove('offline');
            }
          };
          
          // Online/offline event listener'larƒ±
          window.addEventListener('online', checkOnlineStatus);
          window.addEventListener('offline', checkOnlineStatus);
          
          // ƒ∞lk kontrol
          checkOnlineStatus();
          
        } catch (error) {
          console.error('Service Worker registration failed:', error);
        }
      };
      
      registerSW();
    }
  }, []);

  return null;
}
